// Cloudflare Email Routing Worker

export default {
  async email(message, env, ctx) {
    // 解析邮件信息
    const from = message.from;
    const to = message.to;
    const subject = message.headers.get('subject');
    const rawContent = await message.raw();
    const textContent = await message.text();
    const htmlContent = await message.html();

    // 构建邮件对象
    const email = {
      from: from,
      to: to,
      subject: subject,
      sender: from.split('<')[0].trim(),
      senderEmail: from.match(/<(.+)>/)?.[1] || from,
      time: new Date().toLocaleString(),
      preview: textContent.slice(0, 100),
      content: htmlContent || textContent,
      receivedAt: Date.now()
    };

    // 发送邮件数据到处理Worker
    try {
      const response = await fetch('https://your-worker.workers.dev/email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(email)
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      // 邮件处理成功
      return new Response('Email processed successfully', { status: 200 });
    } catch (error) {
      // 记录错误并返回失败响应
      console.error('Error processing email:', error);
      return new Response('Error processing email', { status: 500 });
    }
  }
};